/* Contenedor principal */
.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f3e5f5;
  overflow: hidden;
  padding: 10px; /* Asegura margen en pantallas pequeñas */
}

/* Carta cerrada */
.closed-card {
  width: 300px;
  height: 200px;
  background-color: #fff;
  border: 2px solid #6a1b9a;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.6s ease;
}

.closed-card:hover {
  transform: scale(1.05);
}

.open-message {
  font-size: 1.8rem;
  color: #6a1b9a;
  font-family: 'Dancing Script', cursive;
}

/* Carta abierta */
.open-card {
  width: 90%; 
  max-width: 1000px;
  max-height: 90vh;
  background-color: #fff;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
  overflow-y: auto;
}

/* Contenedor de scroll */
.content-scroll {
  max-height: 60vh;
  overflow-y: auto;
}

/* Título */
.dear-andre {
  font-family: 'Dancing Script', cursive;
  color: #6a1b9a;
  font-size: 3rem;
  text-align: center;
}

/* Imagen */
.love-image {
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 10px auto;
  display: block;
}

/* Contenedor del corazón */
.heart-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
}

.heart-button {
  background-color: transparent;
  border: none;
  font-size: 3rem;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.heart-button:hover {
  transform: scale(1.2);
}

.heart-message {
  font-size: 1.2rem;
  color: #555;
}

/* Contenedor del contenido */
.content-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin-top: 30px;
}

.poem-container,
.message-container {
  width: 100%;
  max-width: 400px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  position: relative;
}

.poem-container {
  background-color: #fff;
}

.message-container {
  background-color: #ffebee;
}

.poem-text,
.message-text {
  font-style: italic;
  line-height: 1.5;
  color: #333;
}

/* Tulipán */
.tulip {
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: auto;
}

/* Media Query para dispositivos móviles */
@media (max-width: 768px) {
  .open-card {
    width: 100%; 
    padding: 15px;
  }

  .dear-andre {
    font-size: 2.5rem;
  }

  .content-container {
    flex-direction: column;
  }

  .poem-container,
  .message-container {
    width: 100%;
  }

  .heart-button {
    font-size: 2.5rem;
  }
}
